.video-about {
  background-color: @white;
  color: @brown;
  text-align: center;
}

.video-about__wrapper {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  margin: 0 auto;
  padding: 0 20px;

  @media (min-width: @tablet-width) {
    width: 768px;
    padding: 0 60px 40px 60px;
  }

  @media (min-width: @desktop-width) {
    width: 1200px;
    padding: 0 200px 84px 200px;
  }
}

.video-about__information {
  display: flex;
  flex-direction: column;
  gap: 35px;
  flex-wrap: wrap;
  margin: 0;
  padding: 17px 6px 38px 8px;

  @media (min-width: @tablet-width) {
    width: 648px;
    padding: 58px 101px 66px;
  }

  @media (min-width: @desktop-width) {
    width: 800px;
    padding: 62px 139px 64px;
  }
}

.video-about__title {
  margin: 0;
}

.video-about__picture {
  position: relative;
  width: 280px;
  height: 177px;
  background-color: @white;

  @media (min-width: @tablet-width) {
    width: 648px;
    height: 500px;
  }

  @media (min-width: @desktop-width) {
    width: 800px;
    height: 500px;
  }
}

.about-video__button {
  position: absolute;
  content: "";
  z-index: 1;
  width: 48px;
  height: 54px;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  margin: auto;
  background-color: transparent;
  color: @white;
  opacity: 70%;
  border: 0;
  padding: 0;

  &:hover {
    opacity: 1;
  }

  &:active {
    opacity: 30%;
  }
}

.video-about__poster {
  margin: 0 auto;

  @media (min-width: @tablet-width) {
    min-width: 648px;
  }

  @media (min-width: @desktop-width) {
    min-width: 800px;
  }
}
