@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("/source/fonts/ptsans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("/source/fonts/ptsansbold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  font-style: italic;
  font-weight: 400;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  font-style: italic;
  font-weight: 700;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
body {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}
main {
  flex-grow: 1;
}
img {
  display: block;
  max-width: 100%;
  height: auto;
  object-fit: contain;
}
a {
  text-decoration: none;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.button {
  display: block;
  min-height: 58px;
  color: #ffffff;
  background-color: #81b3d2;
  font-family: "PT Sans", sans-serif;
  font-size: 21px;
  line-height: 26px;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  cursor: pointer;
}
.button:hover {
  background-color: #669ec0;
}
.button:active {
  background-color: #5496bd;
  color: rgba(255, 255, 255, 0.3);
}
.button:disabled {
  background-color: #e5e5e5;
}
.button--text-small {
  font-size: 18px;
}
@media (min-width: 768px) {
  .button--text-small {
    font-size: 21px;
  }
}
.title {
  color: #000000;
  background-color: inherit;
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  margin-block: 0;
  padding-inline: 0;
}
@media (min-width: 768px) {
  .title {
    font-size: 21px;
  }
}
.title--left {
  text-align: left;
}
.title--color-light {
  color: #ffffff;
}
.title--big {
  font-size: 21px;
}
@media (min-width: 768px) {
  .title--big {
    font-size: 30px;
    line-height: 26px;
  }
}
@media (min-width: 1200px) {
  .title--big {
    line-height: 30px;
  }
}
@media (min-width: 1200px) {
  .title--size-short {
    width: min-content;
    margin: 0 auto;
  }
}
.description {
  color: #333333;
  background-color: inherit;
  text-align: center;
  text-transform: uppercase;
  margin-block: 0;
  padding-inline: 0;
}
.description--left {
  text-align: left;
}
.description--color-light {
  color: #ffffff;
}
@media (min-width: 1200px) {
  .description--size-short {
    margin: 0 10px;
  }
}
@media (max-width: 767px) {
  .description--size-canyon {
    margin: 0 15px;
  }
}
.description--normal {
  font-weight: 400;
}
@media (min-width: 1200px) {
  .description--retreat {
    margin: 0 35px;
  }
}
.link {
  display: block;
  background-color: inherit;
  color: inherit;
  font-family: "PT Sans", sans-serif;
  font-size: 14px;
  line-height: 21px;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  cursor: pointer;
}
.link--big {
  font-size: 21px;
  line-height: 26px;
}
.disabled {
  pointer-events: none;
  cursor: default;
  background-color: #e5e5e5;
}
.contacts {
  display: flex;
  flex-wrap: wrap;
  background-color: #ffffff;
  color: #000000;
  font-size: 21px;
  line-height: 26px;
  font-weight: 700;
}
.contacts:hover {
  color: #81b3d2;
}
.contacts:active {
  color: #b3b3b3;
}
.signature {
  display: block;
  background-color: inherit;
  text-align: left;
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
  color: #333333;
}
.signature--right {
  text-align: right;
}
.signature--color {
  color: #333333;
}
@media (min-width: 1200px) {
  .signature--color {
    color: #ffffff;
  }
}
.signature--big {
  text-transform: uppercase;
}
.signature--form {
  font-size: 12px;
  opacity: 60%;
}
@media (min-width: 768px) {
  .signature--form {
    font-size: 14px;
    align-self: center;
    opacity: 1;
  }
}
.field {
  width: 280px;
  height: 58px;
  border: none;
  background-color: #f2f2f2;
  color: #000000;
  padding: 16px;
  font-family: "PT Sans", sans-serif;
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  resize: none;
  cursor: pointer;
}
.field::placeholder {
  opacity: 30%;
}
@media (min-width: 768px) {
  .field {
    width: 295px;
  }
}
@media (min-width: 1200px) {
  .field {
    width: 430px;
  }
}
.field:hover {
  background-color: #e5e5e5;
}
.field:active {
  background-color: #ffffff;
  border: 0;
  outline: 4px solid #e5e5e5;
  outline-offset: -4px;
}
.field:disabled {
  opacity: 30%;
}
.field--phone {
  padding: 16px 74px 16px 16px;
}
.field--email {
  padding: 16px 74px 16px 16px;
}
.page {
  font-family: "PT Sans", sans-serif;
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
  height: 100%;
  background-color: #ffffff;
  color: #000000;
}
.page__body {
  min-width: 320px;
  min-height: 100%;
  margin: 0 auto;
  background-color: #e7e5e5;
}
@media (min-width: 1200px) {
  .page__body {
    padding: 0 40px;
  }
}
.main-header {
  min-width: 320px;
  min-height: 56px;
  background-color: #ffffff;
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
}
.main-header__wrapper {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  margin: 0 auto;
  z-index: 3;
}
@media (min-width: 768px) {
  .main-header__wrapper {
    width: 768px;
  }
}
@media (min-width: 1200px) {
  .main-header__wrapper {
    width: 1200px;
  }
}
.main-header__logo {
  position: absolute;
  display: block;
  content: "";
  width: 185px;
  height: 96px;
  top: 0;
  bottom: -40px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1;
  cursor: pointer;
}
.main-header__logo:hover {
  opacity: 60%;
}
.main-header__logo:active {
  opacity: 30%;
}
.main-header__navigation {
  margin: 0 auto;
}
.main-navigation {
  min-width: 320px;
  min-height: 56px;
  background-color: #ffffff;
  color: #000000;
  text-align: center;
  text-transform: uppercase;
}
.main-navigation__wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .main-navigation__wrapper {
    flex-direction: row;
    gap: 185px;
    width: 768px;
    padding: 18px 18px 17px 40px;
  }
}
@media (min-width: 1200px) {
  .main-navigation__wrapper {
    width: 1200px;
    gap: 299px;
    padding: 18px 31px 17px 51px;
  }
}
.main-navigation__list {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  margin-block: 0;
  padding-inline: 0;
  background-color: #81b3d2;
  color: #ffffff;
  list-style-type: none;
}
@media (min-width: 768px) {
  .main-navigation__list {
    width: 251px;
    flex-direction: row;
    gap: 75px;
    padding: 0 10px;
    background-color: #ffffff;
    color: #000000;
  }
}
@media (min-width: 1200px) {
  .main-navigation__list {
    width: 398px;
    gap: 162px;
    padding: 0 40px;
  }
}
.main-navigation__user {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  margin-block: 0;
  padding-inline: 0;
  background-color: #81b3d2;
  color: #ffffff;
  list-style-type: none;
}
@media (min-width: 768px) {
  .main-navigation__user {
    flex-direction: row;
    width: 274px;
    gap: 52px;
    padding: 0 10px;
    background-color: #ffffff;
    color: #000000;
  }
}
@media (min-width: 1200px) {
  .main-navigation__user {
    width: 421px;
    gap: 139px;
    padding: 0 40px;
  }
}
.main-navigation__item {
  display: block;
}
@media (max-width: 767px) {
  .main-navigation__item a,
  .main-navigation__user-item a {
    position: relative;
    font-size: 18px;
    line-height: 26px;
    padding: 17px 70px 12px;
    text-align: center;
    border-top: 1px solid rgba(255, 255, 255, 0.2);
    border-bottom: 1px solid rgba(1, 1, 1, 0.2);
  }
  .main-navigation__item a:hover,
  .main-navigation__user-item a:hover {
    background-color: #669ec0;
  }
  .main-navigation__item a:active,
  .main-navigation__user-item a:active {
    background-color: #5496bd;
    color: rgba(255, 255, 255, 0.2);
  }
}
@media (min-width: 768px) {
  .main-navigation__item a:hover,
  .main-navigation__user-item a:hover {
    color: #81b3d2;
  }
  .main-navigation__item a:active,
  .main-navigation__user-item a:active {
    color: #000000;
    opacity: 30%;
  }
}
.main-navigation__user-item:last-child a {
  border-bottom: 0;
}
@media (min-width: 768px) {
  .main-navigation__item--current {
    color: #766357;
  }
}
.main-navigation__toggle {
  position: absolute;
  content: "";
  top: 0;
  bottom: 0;
  right: 0;
  display: block;
  margin-right: auto;
  padding: 0;
  width: 68px;
  height: 56px;
  background-color: #ffffff;
  border: 0;
  z-index: 3;
  cursor: pointer;
}
@media (min-width: 768px) {
  .main-navigation__toggle {
    display: none;
    z-index: 0;
  }
}
.main-navigation__toggle:hover {
  opacity: 60%;
}
.main-navigation__toggle:active {
  opacity: 30%;
}
.main-navigation--closed .main-navigation__toggle::before {
  position: absolute;
  content: "";
  top: 15px;
  right: 20px;
  width: 23px;
  height: 4px;
  z-index: 2;
  background-color: #81b3d2;
  box-shadow: 0 9px 0 0 #81b3d2, 0 18px 0 0 #81b3d2;
}
@media (max-width: 767px) {
  .main-navigation--closed .main-navigation__wrapper {
    display: none;
  }
}
.main-navigation--opened .main-navigation__toggle {
  background-color: #81b3d2;
}
.main-navigation--opened .main-navigation__toggle::before,
.main-navigation--opened .main-navigation__toggle::after {
  position: absolute;
  content: "";
  top: 25px;
  right: 20px;
  width: 32px;
  height: 4px;
  background-color: #ffffff;
}
.main-navigation--opened .main-navigation__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-navigation--opened .main-navigation__toggle::after {
  transform: rotate(-45deg);
}
@media (max-width: 767px) {
  .main-navigation--opened .main-navigation__wrapper {
    position: absolute;
    content: "";
    top: 0;
    right: 0;
    left: 0;
    z-index: 2;
    min-height: 80vh;
  }
}
@media (max-width: 767px) {
  .main-navigation--opened .main-navigation__wrapper::after {
    position: absolute;
    content: "";
    top: 219px;
    left: 0;
    width: 100%;
    height: 7px;
    z-index: 2;
    background: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.2));
  }
}
.main-navigation--nojs .main-navigation__toggle {
  display: none;
}
.main-navigation--nojs .main-navigation__wrapper {
  position: static;
  min-height: 0;
}
.main-navigation--nojs .main-navigation__item,
.main-navigation--nojs .main-navigation__user-item {
  display: block;
}
